  .cardContent {
    overflow: hidden;
    width: 94vw;
    height: 80vh;
    border-radius: 20px; /* This is the rounded corner radius */
    background: #FDE0CC;
  }
  
  .scrollableArea {
    overflow-y: auto;
    height: 80vh; /* Set a fixed height for the scrolling area */
  }
  .scrollableArea::-webkit-scrollbar {
    display: none;
  }
  
  .scrollableArea {
    -ms-overflow-style: none;  /* IE and Edge */
    scrollbar-width: none;  /* Firefox */
  }
  .card-bio, .card-basics h3 {
    font-size: 1.5rem;
    color: #6B6B6B;
    font-weight: 500;
    /* padding: 5px 20px; */
  }
    .card-bio, .card-basics {
        font-size: 1.2rem;
        color: #212121;
        /* font-weight: 600; */
        padding: 0px 20px;
        margin: 10px 0px;
    }
    
    .description {
        display: inline-block;
        position: relative;
        background: #F8C79A;
        font-size: 1.1rem;
        margin: 10px 5px;
        padding: 5px 10px;
        border-radius: 20px;   
    }
  

.card__no-more{
    position: absolute;
    width: 70%;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    font-size: 1.5rem;
    font-weight: 400;
    text-align: center;
    
}
.card__no-more > p{
    font-size: 1rem;
    font-weight: 400;
    padding-top: 20px;
}

   .explore-header {
    display: flex;
    /* position: fixed; */
    width: 90%;
    justify-content: space-between;
    padding: 0px 20px;
    align-items: center;
    background: #fff; 
  }
  .explore-header h2{
    font-size: 1.5rem;
